import React, {useState, useEffect} from 'react';
import axios from 'axios';
import { Card } from 'react-bootstrap';
import {variable} from '../global/url';
import DeleteComp from '../delete/DeleteComp';
import EditComp from '../edit/EditComp';


export default function OwnerDetail({match}) {
    const [owner, setOwner] = useState({})
    const id = match.params.id

    useEffect( async () => {
        await axios({
            method: 'GET',
            url: variable.MainUrl + `api/v1/owner/${id}`
        }).then(response => {
            setOwner(response.data)
        })
    }, [id])

    return(
        <div className="container">
          <Card border="dark">
            <Card.Header>
            <div className="d-flex">
             <div className="me-5">Category detail</div>
                <div className="d-flex justify-content-around">
                    <EditComp id={id} name={'owner'}/>
                    <DeleteComp id={id} name={'owner'}/>
                </div>
              </div>
            </Card.Header>
            <Card.Body>
              <Card.Title>{owner.surname} {owner.name}</Card.Title>
              <Card.Text>
                {owner.owner_description}
              </Card.Text>
            </Card.Body>
          </Card>
        </div>
    )
}

