import React, {useState, useEffect} from 'react';
import axios from 'axios';
import { Card } from 'react-bootstrap';
import {variable} from '../global/url';
import DeleteComp from '../delete/DeleteComp';
import EditComp from '../edit/EditComp';


function OwnerDetail({match}) {
    const [departament, setDepartament] = useState({})
    const id = match.params.id

    useEffect( async () => {
        await axios({
            method: 'GET',
            url: variable.MainUrl + `api/v1/departament/${id}`
        }).then(response => {
            setDepartament(response.data)
        })
    }, [id])

    return(
        <div className="container">
          <Card border="dark">
            <Card.Header>
            <div className="d-flex">
             <div className="me-5">Departament detail</div>
                <div className="d-flex justify-content-around">
                    <EditComp id={id} name={'departament'}/>

                    <DeleteComp id={id} name={'departament'}/>
                </div>
              </div>
            </Card.Header>
            <Card.Body>
              <Card.Title>{departament.departament_title}</Card.Title>
              <Card.Text>
                {departament.departament_description}
              </Card.Text>
            </Card.Body>
          </Card>
        </div>
    )
}

export default OwnerDetail;